import React, {memo, useEffect} from 'react';
import {HeaderEvent} from '@/screens/DetailEventScreen/HeaderEvent';
import {CoverInfoEvent} from '@/screens/DetailEventScreen/CoverInfoEvent';
import {FullScreenWrapper, ScreenWrapper, ScrollableTabView} from '@true/ui';
import {View} from 'react-native';
import {OverviewEvent} from '@/screens/DetailEventScreen/OverviewEvent';
import {AttendeesEvent} from '@/screens/DetailEventScreen/AttendeesEvent';
import {OtherEvents} from '@/screens/DetailEventScreen/OtherEvents';
import {useEvents} from '@/store/events';
import {useAsyncFn, useNavigationParams} from '@true/core';
import {requestEvent} from '@/store/events/function';
import {EventInformation} from '@/screens/DetailEventScreen/EventInformation';

export interface DetailEventScreenParams {
  id: number;
}

export const DetailEventScreen = memo(function () {
  const {id} = useNavigationParams<DetailEventScreenParams>();
  const data = useEvents(id);

  const [{loading: firstLoading, error: firstError}, loadFirst] = useAsyncFn(
    () => requestEvent({event_id: id}),
    [id],
  );

  useEffect(() => {
    loadFirst().then();
  }, []);

  return (
    <FullScreenWrapper isGrey={true}>
      <HeaderEvent id={id} />
      <ScrollableTabView.Provider
        index={0}
        level={0}
        tabs={[
          {
            title: 'Overview',
            content: p => <OverviewEvent index={p.index} id={id} />,
          },
          {
            title: 'Attendees',
            content: p => <AttendeesEvent data={data} index={p.index} />,
          },
          {
            title: 'Detail',
            content: p => <EventInformation id={id} index={p.index} />,
          },
          {
            title: 'Other events',
            content: p => <OtherEvents index={p.index} id={id} />,
          },
        ]}>
        <ScrollableTabView.CollapsableTabBar>
          <CoverInfoEvent id={id} />
        </ScrollableTabView.CollapsableTabBar>
        <View style={{flex: 1, marginTop: 8}}>
          <ScrollableTabView.Content />
        </View>
      </ScrollableTabView.Provider>
    </FullScreenWrapper>
  );
});
export default DetailEventScreen;
