import React, {memo, useCallback} from 'react';
import {
  Comment,
  CommentPreview,
  MyKeyboardAvoidingView,
  ScrollableTabView,
  UIText,
} from '@true/ui';
import {EventDescription} from '@/screens/DetailEventScreen/EventDescription';
import {useEvents} from '@/store/events';
import {NativeScrollEvent, NativeSyntheticEvent, View} from 'react-native';
import {CommentEvent} from '@/screens/DetailEventScreen/CommentEvent';
import {globalStyles} from '@/utils/globalStyle';
import {styled} from '@true/core';

interface OverviewEventProps {
  id: number;
  index: number;
}

export const OverviewEvent = memo(function (props: OverviewEventProps) {
  const {id, index} = props;
  const data = useEvents(id);

  return (
    <MyKeyboardAvoidingView>
      <CommentPreview.Provider
        appName={'inside'}
        type={'comment'}
        hid={data?.hid || ''}
        token={data?.token || ''}>
        <ScrollableTabView.ScrollView
          index={index}
          keyboardShouldPersistTaps="handled">
          <EventDescription content={data?.content} attachments={data?.files} />
          <CommentEvent />
        </ScrollableTabView.ScrollView>
        <CommentPreview.Input />
      </CommentPreview.Provider>
    </MyKeyboardAvoidingView>
  );
});
