import { memo } from "react";
import {styled} from "@true/core";
import { Fonts } from "@true/core";
import { HTMLRenderer, UIText } from "@true/ui";
import { View } from "react-native";
import { globalStyles } from "@/utils/globalStyle";
import { AttachmentFile } from "@/components/AttachmentFile";
import { IAttachmentFile } from "@/components/AttachmentFile/type";

interface EventDescriptionProps {
  content: string | undefined;
  attachments?: IAttachmentFile[];
}

export const EventDescription = memo(function(props: EventDescriptionProps) {
  const { content, attachments } = props;
  return (
    <SContainer>
      <UIText.HeaderMedium style={globalStyles.mb12}>Description</UIText.HeaderMedium>
      <View
        style={{
          overflow: "hidden",
          backgroundColor: "white"
          // maxHeight: !htmlExtended ? 135 : undefined,
        }}>
        <HTMLRenderer
          htmlContent={content || ""}
          defaultTextProps={{
            style: {
              fontSize: 14,
              lineHeight: 20,
              color: "#333"
            }
          }}
        />
      </View>
      <UIText.HeaderMedium style={globalStyles.mt12}>Attachments</UIText.HeaderMedium>
      <AttachmentWrapper>
        {attachments?.map((file, index) => {
          return (
            <AttachmentFile key={index} attachment={file} time={file.since} />
          );
        })}
      </AttachmentWrapper>
    </SContainer>
  );
});
const SContainer = styled.View`
  padding: 8px 16px;
  background-color: white;
`;
const STitle = styled.Text`
  font-family: ${Fonts.Regular};
  
  font-weight: 700;
  font-size: 16px;
  color: #242424;
`;

const AttachmentWrapper = styled.View`
  flex-direction: row;
  flex-wrap: wrap;
`;
