import {Fonts} from '@true/core';
import {memo, useCallback} from 'react';
import {styled} from '@true/core';
import {EventThumbnail} from '@/screens/EventScreen/EventThumbnail';
import {useEventsByQuery} from '@/store/events';
import {ScrollableList} from '@true/ui';
import {EmptyView} from '@/components/EmptyView';

interface OtherEventsProps {
  id: number;
  index: number;
}

export const OtherEvents = memo(function (props: OtherEventsProps) {
  const {id, index} = props;
  const listOtherEvents = useEventsByQuery('all').filter(item => {
    return item !== id.toString();
  });
  const renderItem = useCallback(({item}: any) => {
    return <EventThumbnail id={item} />;
  }, []);
  return (
    <ScrollableList
      data={listOtherEvents}
      ListEmptyComponent={() => <EmptyView text="No event was found" />}
      ListHeaderComponent={() => (
        <SHeader>
          <STitle>Other events</STitle>
        </SHeader>
      )}
      loading={false}
      renderItem={renderItem}
      index={index}
    />
  );
});
const SHeader = styled.View`
  padding: 8px 16px;
`;
const STitle = styled.Text`
  font-family: ${Fonts.Bold};
  font-size: 16px;
  color: #242424;
`;
