import React, { memo } from "react";
import Animated, { useAnimatedStyle } from "react-native-reanimated";
import { useAnimatedSafeAreaInsets } from "react-native-safe-area-context";
import { goBack } from "@true/core";
import { IC_OUTLINED_LEFT } from "@true/asset";
import { UIButton } from "@true/ui";
import { useEvents } from "@/store/events";
import { PostImage } from "@/components/PostThumbnail/PostImage";
import { StyleSheet, View } from "react-native";
import { useSetStatusBar } from "@/hooks/useSetStatusBar";
import { hexToRgbA } from "@/utils";
import LinearGradient from "react-native-linear-gradient";

interface HeaderEventProps {
  id: number;
}

export const HeaderEvent = memo(function(props: HeaderEventProps) {
  const { id } = props;
  const data = useEvents(id);
  const { aTop } = useAnimatedSafeAreaInsets();
  const animatedStyle = useAnimatedStyle(() => ({
    paddingTop: aTop.value
  }));
  useSetStatusBar();

  return (
    <View>
      <View style={{ height: 218, width: "100%" }}>
        <PostImage image={data?.cover} />
        <Animated.View style={[StyleSheet.absoluteFillObject]}>
          <Animated.View style={animatedStyle}>
            <Animated.View style={[StyleSheet.absoluteFillObject]}>
              <LinearGradient
                colors={[hexToRgbA("#101012", 0), hexToRgbA("#000000", 1)]}
                style={{
                  height: "100%",
                  width: "100%",
                  zIndex: 1,
                  transform: [{ rotateZ: "180deg" }]
                }}
              />
            </Animated.View>
            <UIButton
              source={IC_OUTLINED_LEFT}
              style={{
                height: 44,
                marginLeft: 16,
                justifyContent: "center"
              }}
              imageStyle={{
                tintColor: "white"
              }}
              onPress={goBack}
            />
          </Animated.View>
        </Animated.View>
      </View>
    </View>
  );
});
