import {styled, useUser} from '@true/core';
import {Avatar, UIButton, UIText} from '@true/ui';
import {memo, useCallback} from 'react';
import {View} from 'react-native';
import {IC_OUTLINED_MORE_HORIZ} from '@true/asset';
import {navigateToProfileScreen} from '@/utils';

interface AttendeeItemProps {
  id: number;
}

export const AttendeeItem = memo(function (props: AttendeeItemProps) {
  const {id} = props;
  const user = useUser(id);
  const onNavigateToProfile = useCallback(() => {
    navigateToProfileScreen({
      username: user?.username || '',
      user_id: user?.id,
    });
  }, []);
  return (
    <SContainer>
      <Avatar userKey={user?.id || ''} size={40} />
      <SInfoWrapper>
        <View
          style={{
            flex: 1,
          }}>
          <UIText.BodyMedium400>{user?.name}</UIText.BodyMedium400>
          <STextJob numberOfLines={1}>
            {user?.title || 'No job description'}
          </STextJob>
        </View>
        <UIButton
          source={IC_OUTLINED_MORE_HORIZ}
          onPress={onNavigateToProfile}
        />
      </SInfoWrapper>
    </SContainer>
  );
});
const SContainer = styled.View`
  flex-direction: row;
  align-items: flex-start;
  padding: 8px 16px;
  gap: 12px;
  background-color: ${p => p.theme.backgroundColor};
`;

const SInfoWrapper = styled.View`
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  border-bottom-width: 1px;
  border-bottom-color: #ebebeb;
  padding-bottom: 16px;
  flex: 1;
`;
const STextJob = styled(UIText.CaptionMedium400)`
  color: ${p => p.theme.neutral2};
  flex: 1;
`;
