import {styled, useUser} from '@true/core';
import {memo, useMemo} from 'react';
import {Avatar, ScrollableTabView, UIText} from '@true/ui';
import {globalStyles} from '@/utils/globalStyle';
import {IC_FILLED_GROUP, IC_FILLED_HUB, IC_FILLED_PUBLIC} from '@true/asset';
import {useEvents} from '@/store/events';
import {getChannels} from '@/store/channels';

interface EventInformationProps {
  id: number;
  index: number;
}

export const EventInformation = memo(function (props: EventInformationProps) {
  const {id, index} = props;
  const data = useEvents(id);
  const user = useUser(data?.user_id || '');
  const channelName = useMemo(() => {
    return getChannels(data?.channel_id || '')?.name;
  }, []);
  return (
    <ScrollableTabView.ScrollView index={index}>
      <SContainer>
        <UIText.HeaderMedium style={globalStyles.mb12}>
          Event information
        </UIText.HeaderMedium>
        <RowInformation>
          <Avatar userKey={user?.id || ''} size={20} />
          <STextNormal style={globalStyles.ml4}>
            Created by <STextBold>{user?.name}</STextBold> at 30 days ago
          </STextNormal>
        </RowInformation>
        <RowInformation>
          <SIcon>
            <SImage source={IC_FILLED_GROUP} />
          </SIcon>
          <STextNormal style={globalStyles.ml4}>
            {data?.attendees?.length} people joined
          </STextNormal>
        </RowInformation>
        <RowInformation>
          <SIcon>
            <SImage source={IC_FILLED_PUBLIC} />
          </SIcon>
          <STextNormal style={globalStyles.ml4}>
            This is a {data?.privacy === 0 ? 'public' : 'private'} event
          </STextNormal>
        </RowInformation>
        <RowInformation>
          <SIcon>
            <SImage source={IC_FILLED_HUB} />
          </SIcon>
          <STextNormal
            style={globalStyles.ml4}>{`Channel: ${channelName}`}</STextNormal>
        </RowInformation>
      </SContainer>
    </ScrollableTabView.ScrollView>
  );
});
const SContainer = styled.View`
  padding: 8px 16px;
  background-color: white;
`;
const STextNormal = styled(UIText.BodyMedium400)`
  flex: 1;
  margin-left: 12px;
`;
const STextBold = styled(STextNormal)`
  color: ${p => p.theme.primaryColor};
`;
const RowInformation = styled.View`
  flex-direction: row;
  align-items: center;
  margin: 8px 0;
  gap: 12px;
`;
const SIcon = styled.View`
  width: 20px;
  height: 20px;
  align-items: center;
  justify-content: center;
`;
const SImage = styled.Image`
  width: 20px;
  height: 20px;
`;
