import React, { memo, useCallback, useMemo } from "react";
import { Fonts, styled, useAsyncFn, useMe } from "@true/core";
import {
  IC_FILLED_CHECK,
  IC_FILLED_EVENT,
  IC_FILLED_LOCATION_ON,
  IC_FILLED_PERSON_ADD,
  IC_OUTLINED_MORE_HORIZ
} from "@true/asset";
import moment from "moment/moment";
import { FlexCenter, SLineDivider } from "@/utils/helper";
import { UIButton, UIText, UITextStyle } from "@true/ui";
import Animated from "react-native-reanimated";
import { getChannels } from "@/store/channels";
import { useEvents } from "@/store/events";
import { EventActionBottomSheet } from "@/screens/DetailEventScreen/BottomSheet/EventAction";
import { BottomSheetModal } from "@gorhom/bottom-sheet";
import { requestAttendEvent, requestInviteEvent } from "@/store/events/function";
import { navigateToAddMemberScreen } from "@/utils";

interface CoverEventProps {
  id: number;
}

export const CoverInfoEvent = memo(function(props: CoverEventProps) {
  const { id } = props;
  const data = useEvents(id);
  const me = useMe();
  const bottomSheetRef = React.useRef<BottomSheetModal>(null);

  const channelName = useMemo(() => {
    return getChannels(data?.channel_id || "")?.name;
  }, []);
  const isAdmin = useMemo(() => {
    return me?.id && data?.owners.includes(me?.id);
  }, [me]);
  const [{ loading }, onAttend] = useAsyncFn(async () => {
    if (data && me?.id) {
      await requestAttendEvent({ id: id, data, myId: me?.id });
    }
  }, [data, me]);

  const onAddAttendees = useCallback(
    async (ids: number[], usernameIds: string) => {
      await requestInviteEvent({
        id,
        channel_id: data?.channel_id || "",
        ids,
        guests: usernameIds,
        data
      });
    },
    []
  );

  const onAddAttendee = useCallback(() => {
    bottomSheetRef.current?.close();
    navigateToAddMemberScreen({
      ids: data?.attendees || [],
      onSubmit: onAddAttendees
    });
  }, []);
  return (
    <SContainer>
      <SChannel>{channelName}</SChannel>
      <SContentContainer>
        <UIText.HeaderLarge>{data?.name}</UIText.HeaderLarge>
        <SRowInfo>
          <SIcon source={IC_FILLED_EVENT} />
          <SInfo>
            {moment(
              // @ts-ignore
              data?.time * 1000
            ).format("dddd HH:mm DD/MM/YYYY")}
          </SInfo>
          {data?.time && data?.time * 1000 < moment().valueOf() ? (
            <FlexCenter>
              <SLineDivider />
              <STextEnd>Ended</STextEnd>
            </FlexCenter>
          ) : null}
        </SRowInfo>
        <SRowInfo>
          <SIcon source={IC_FILLED_LOCATION_ON} />
          <SInfo>{data?.address}</SInfo>
        </SRowInfo>
        <SRowInfo>
          {me?.id ? (
            data?.attendees?.includes(Number(me?.id)) ? (
              <SOutlinedAttend
                loading={loading}
                onPress={onAttend}
                source={IC_FILLED_CHECK}
                title={"Attending"}
                imageSize={20}
              />
            ) : (
              <SPrimaryButton
                loading={loading}
                onPress={onAttend}
                source={IC_FILLED_CHECK}
                title={"Attend"}
                imageSize={20}
              />
            )
          ) : null}
          <SOutlinedButton
            onPress={onAddAttendee}
            source={IC_FILLED_PERSON_ADD}
          />
          {isAdmin ? (
            <SOutlinedButton
              source={IC_OUTLINED_MORE_HORIZ}
              onPress={() => {
                bottomSheetRef.current?.present();
              }}
            />
          ) : null}
        </SRowInfo>
      </SContentContainer>
      <EventActionBottomSheet ref={bottomSheetRef} id={id} />
    </SContainer>
  );
});

const SContainer = styled(Animated.View)`
  padding: 8px 16px;
  background-color: white;
`;
const SContentContainer = styled(Animated.View)`
  gap: 12px;
  margin-top: 4px;
`;

const SChannel = styled(UIText.BodyMedium400)`
  color: #267cde;
`;
const SRowInfo = styled.View`
  flex-direction: row;
  align-items: center;
  gap: 4px;
`;
const SIcon = styled.Image`
  width: 20px;
  height: 20px;
  tint-color: ${p => p.theme.neutral2};
  margin-right: 4px;
`;
const SInfo = styled(UIText.BodyMedium400)`
  color: ${p => p.theme.neutral2};
`;
const STextEnd = styled.Text`
  color: ${p => p.theme.red1};
  /* Body/12R */
  font-family: ${Fonts.Regular};
  font-size: 12px;
  line-height: 16px; /* 133.333% */
`;
const SOutlinedButton = styled(UIButton).attrs(p => ({
  imageStyle: {
    tintColor: p.theme.primaryColor
  }
}))`
  border-radius: 8px;
  border: 1px solid ${p => p.theme.primaryColor};
  display: flex;
  width: 40px;
  height: 40px;
  padding: 4px 16px;
  justify-content: center;
  align-items: center;
`;
const SPrimaryButton = styled(UIButton).attrs(p => ({
  textStyle: {
    ...UITextStyle.button_large,
    color: "white"
  },
  imageStyle: {
    tintColor: "white"
  }
}))`
  display: flex;
  flex: 1;
  height: 40px;
  padding: 4px 16px;
  justify-content: center;
  align-items: center;
  border-radius: 8px;
  background-color: ${p => p.theme.primaryColor};
`;
const SOutlinedAttend = styled(UIButton).attrs(p => ({
  textStyle: {
    ...UITextStyle.button_large,
    color: p.theme.primaryColor
  },
  imageStyle: {
    tintColor: p.theme.primaryColor
  }
}))`
  display: flex;
  flex: 1;
  height: 40px;
  padding: 4px 16px;
  justify-content: center;
  align-items: center;
  border-radius: 8px;
  border: 1px solid ${p => p.theme.primaryColor};
`;
