import React, { memo } from "react";
import { styled, useTheme } from "@true/core";
import { FlexCenter, FlexSpaceBetween } from "@/utils/helper";
import { View } from "react-native";
import { IC_OUTLINED_SEARCH } from "@true/asset";
import { ScrollableList, SingleTabView, UIButton, UIText } from "@true/ui";
import { IEvent } from "@/store/events/type";
import { EmptyView } from "@/components/EmptyView";
import { AttendeeItem } from "@/screens/DetailEventScreen/AttendeeItem";

interface AttendeesEventProps {
  data: IEvent | undefined;
  index: number;
}

export const AttendeesEvent = memo(function(props: AttendeesEventProps) {
  const { data, index } = props;
  const theme = useTheme();
  return (
    <SingleTabView.Provider
      index={index}
      tabs={[
        {
          title: "All attendees",
          content: p => (
            <ScrollableList
              data={data?.attendees || []}
              ListEmptyComponent={<EmptyView text="No attendee was found" />}
              loading={false}
              renderItem={({ item }: any) => {
                return <AttendeeItem id={item} />;
              }}
              singleListIndex={p.singleListIndex}
              index={index}
            />
          )
        },
        {
          title: "Guests",
          content: p => (
            <ScrollableList
              data={data?.followers || []}
              ListEmptyComponent={<EmptyView text="No guest was found" />}
              loading={false}
              renderItem={({ item }: any) => {
                return <AttendeeItem id={item} />;
              }}
              singleListIndex={p.singleListIndex}
              index={index}
            />
          )
        },
        {
          title: "Pending approvals",
          content: p => (
            <ScrollableList
              data={data?.invites || []}
              ListEmptyComponent={<EmptyView text="No pending approvals" />}
              loading={false}
              renderItem={({ item }: any) => {
                return <AttendeeItem id={item} />;
              }}
              singleListIndex={p.singleListIndex}
              index={index}
            />
          )
        }
      ]}>
      <SingleTabView.CollapsableTabBar hideSeparator>
        <SContainer>
          <FlexSpaceBetween>
            <UIText.HeaderMedium>Attendees</UIText.HeaderMedium>
            <FlexCenter>
              {/*<UIButton source={IC_OUTLINED_PERSON_ADD}/>*/}
              <UIButton source={IC_OUTLINED_SEARCH} />
            </FlexCenter>
          </FlexSpaceBetween>
        </SContainer>
      </SingleTabView.CollapsableTabBar>
      <View
        style={{
          flex: 1,
          backgroundColor: "transparent"
        }}>
        <SingleTabView.Content />
      </View>
    </SingleTabView.Provider>
  );
});
const SContainer = styled.View`
  padding: 8px 16px;
  background-color: white;
`;
